let menuItems = [], clonesHeight = 0, scrollHeight = 0, context, scrollPos;


const winsize = {width: window.innerWidth, height: window.innerHeight};

let getScrollPos = () => {
    return (context.pageYOffset || context.scrollTop) - (context.clientTop || 0);
}

let setScrollPos = (pos) => {
    context.scrollTop = pos;
}

// Create menu items clones and append them to the menu items list
// total clones = number of menu items that fit in the viewport
let cloneItems = () => {
    if(document.getElementById('inpg02') === undefined || document.getElementById('inpg02') === null) return;     // catuion
    // Get the height of one menu item
    const itemHeight = menuItems[0].offsetHeight;
    // How many items fit in the window?
    const fitIn = Math.ceil(winsize.height / itemHeight);
    // Create [fitIn] clones from the beginning of the list
        
    // Remove any
    document.querySelectorAll('.loop__clone').forEach(clone => document.getElementById('sklcnt').removeChild(clone));
    // Add clones
    let totalClones = 0;
    menuItems.filter((_, index) => (index < fitIn)).map((target) => {
        const clone = target.cloneNode(true);
        clone.classList.add('loop__clone');
        document.getElementById('sklcnt').appendChild(clone);
        ++totalClones;
    });

    // fixing scrolling bug due to framer
    let eleClones = document.getElementsByClassName('loop__clone');
    for(let i=0; i<eleClones.length; i++) {
        eleClones[i].style.removeProperty('transform');
    }

    // All clones height
    clonesHeight = totalClones * itemHeight;
    // Scrollable area height
    scrollHeight = document.getElementById('inpg02').scrollHeight;
}

let initEvents = () => {
    window.addEventListener('resize', () => resize());
}

let resize = () => {
    cloneItems();
    initScroll();
}

let initScroll = () => {
    // Scroll 1 pixel to allow upwards scrolling
    let scrollPos = getScrollPos();
    if (scrollPos <= 0) {
        setScrollPos(1);
    }
}

let scrollUpdate = () => {  
    scrollPos = getScrollPos();

    if(clonesHeight + scrollPos >= scrollHeight) {
        // Scroll to the top when youve reached the bottom
        setScrollPos(1); // Scroll down 1 pixel to allow upwards scrolling
    } 
    else if(scrollPos <= 0 ) {
        // Scroll to the bottom when you reach the top
        setScrollPos(scrollHeight - clonesHeight);
    }
}
    
let render = () => {
    scrollUpdate();
    requestAnimationFrame(() => render());
}

let mainScroll = () => setTimeout(() => {
    context = document.querySelector('.inner-pg-02');
    menuItems = [...document.querySelectorAll('.skl-link')];
    cloneItems();
    initScroll();

    initEvents();

    // rAF/loop
    requestAnimationFrame(() => render());
}, 100);

export { mainScroll };
