// animate welcome page
var oldHref = document.location.href;
// variables
let timeOutFunctionID;
let cordinates = [];
let radius = [];
let rd, x, y = 0;
// generating random values
let getRandom = (min, max) => {
    return Math.floor(Math.random() * (max - min) + min);
}

// calculating ditance between two cordinates
let distance = (x1, y1, x2, y2)=> {
    return Math.sqrt(Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2));
}

// get position of elements
let getPosition = (i) => {
    let ele = document.getElementsByClassName('background-blob')
    let offsets = ele[i].getBoundingClientRect();
    return {
        x: offsets.left + offsets.width / 2,
        y: offsets.top + offsets.height / 2,
        r: offsets.width / 2
    }
}

// updating blobs according to screen size
let updateBlob = () => {
    // console.log(new Date() + " started updated or block of lines from # to #");
    let eleData = setPosition();
    if(eleData.blb.length === 0) {return};
    // console.log(eleData.blb)
    for(let i=0; i<eleData.blb.length; i++) {
        eleData.blb[i].style.display = "none";
    }

    for(let i=0; i<eleData.cd.length; i++) {
        eleData.blb[i].style.left = eleData.cd[i].x + "px";
        eleData.blb[i].style.top = eleData.cd[i].y + "px";
        eleData.blb[i].style.width = (eleData.rad[i].r * 2) + "px";
        eleData.blb[i].style.height = (eleData.rad[i].r * 2) + "px";
        eleData.blb[i].style.display = "block";
    }

    // on resizing updating blobs positions
    window.addEventListener('resize', () => {
        if(window.innerWidth < 800) {
            // updateBlob();
            clearTimeout(timeOutFunctionID);
            timeOutFunctionID = setTimeout(updateBlob, 1000);
        }
        else {
            // updateBlob();
            clearTimeout(timeOutFunctionID);
            timeOutFunctionID = setTimeout(updateBlob, 1000);
        }
    });
    // console.log(new Date() + " finished updated or block of lines from # to #")
}

let randomPos = (lower, upper, innerHeight, innerWidth) => {
    rd = getRandom(lower, upper) / 2;
    x = getRandom(1, innerWidth - 200);
    y = getRandom(1, innerHeight - 100);

    // stroing positions and radius of circles
    cordinates.push({x: x, y: y});
    radius.push({r: rd});
} 

// positioning elements without overlapping
let setPosition = () => {
    // console.log(new Date() + " started positioning or block of lines from # to #");

    // console.log("positioning" + (++counts));
    cordinates = [];
    radius = [];

    const blobBg = document.getElementsByClassName('background-blob');
    let innerWidth = window.innerWidth;
    let innerHeight = window.innerHeight;

    // generating random x, y and radius 
    if(innerWidth > 800) {
        randomPos(500, 700, innerHeight, innerWidth);
    }
    else {
        randomPos(300, 400, innerHeight, innerWidth);
    }

    // console.log(new Date() + " finished positioning or block of lines from # to #")
    return {cd: cordinates, rad : radius, blb :blobBg};
}

let newElement = (tagName, id, idName, clas, className, parentHead, content) => {
    let tag = document.createElement(tagName);
    tag.textContent = content;
    tag.setAttribute(clas, className);
    tag.setAttribute(id, idName);
    parentHead.appendChild(tag);
    return tag;
}

let names = (strName, clsNm, idNm, eles, innerCls) => {
    for(let i=0; i<strName.length; i++) {
        console.log(strName[i], clsNm[i], idNm[i], eles);
        newElement("span", "id", idNm[i], "class", clsNm[i], eles[0], "");
        let ele = document.getElementById(idNm[i]);
        let str = strName[i];
        for(let j=0; j<strName[i].length; j++) {
            if(str[j] === '.')  newElement("span", "id", idNm[i] + j, "class", "letend", ele, str[j]);
            else newElement("span", "id", idNm[i] + j, "class", innerCls, ele, str[j]);
        }
    }
}

