    let clsNm = [["firstName", "lastName break"], ["sec sec1", "sec sec2 break", "sec sec3 break", "sec sec4 break", "sec sec5 break", "sec sec6 break", "sec sec7 break"]];
    let innerCls = [["rubber"], ["flip"]];
