// animating text on hover
let animate = () => {
    // name = "Aman Kumar."

    let animateEle = document.getElementsByClassName("rubber");
    for (let i = 0; i <animateEle.length; i++) {
        animateEle[i].addEventListener("animationend", () => {
            animateEle[i].classList.remove("animate");
        });

        animateEle[i].addEventListener("mouseover", () => {
            animateEle[i].classList.add("animate");
        });
    }
}

export {dynamicText, animate, updateBlob};
