let focus = () => {
    let ele = document.getElementsByClassName('pro-name');
    let menuActive1 = document.getElementById("pn01");
    let menuShow1 = document.getElementById("pp01");
    let menuActive2 = document.getElementById("pn02");
    let menuShow2 = document.getElementById("pp02");
    let menuActive3 = document.getElementById("pn03");
    let menuShow3 = document.getElementById("pp03");

    let addnRemoveClassMenu1 = (addAct, addShw, removeAct, removeShw)  => {
        menuActive1.classList.add(addAct);
        menuShow1.classList.add(addShw);
        menuActive1.classList.remove(removeAct);
        menuShow1.classList.remove(removeShw);
    }

    let addnRemoveClassMenu2 = (addAct, addShw, removeAct, removeShw)  => {
        menuActive2.classList.add(addAct);
        menuShow2.classList.add(addShw);
        menuActive2.classList.remove(removeAct);
        menuShow2.classList.remove(removeShw);
    }

    let addnRemoveClassMenu3 = (addAct, addShw, removeAct, removeShw)  => {
        menuActive3.classList.add(addAct);
        menuShow3.classList.add(addShw);
        menuActive3.classList.remove(removeAct);
        menuShow3.classList.remove(removeShw);
    }



    ele[0].addEventListener('click', () => {
        if(!menuActive1.classList.contains('active') && !menuShow1.classList.contains('show')) {
            if(menuActive2.classList.contains('active') && menuShow2.classList.contains('show')) {
                addnRemoveClassMenu2('not-active', 'hide', 'active', 'show');
                addnRemoveClassMenu1('active', 'show', 'not-active', 'hide');
            }
            else if(menuActive3.classList.contains('active') && menuShow3.classList.contains('show')) {
                addnRemoveClassMenu3('not-active', 'hide', 'active', 'show');
                addnRemoveClassMenu1('active', 'show', 'not-active', 'hide');
            }
            else {
                return;
            }
        }
        else {
            return;
        }
    })

    ele[1].addEventListener('click', () => {
        if(!menuActive2.classList.contains('active') && !menuShow2.classList.contains('show')) {
            if(menuActive1.classList.contains('active') && menuShow1.classList.contains('show')) {
                addnRemoveClassMenu1('not-active', 'hide', 'active', 'show');
                addnRemoveClassMenu2('active', 'show', 'not-active', 'hide');
            }
            else if(menuActive3.classList.contains('active') && menuShow3.classList.contains('show')) {
                addnRemoveClassMenu3('not-active', 'hide', 'active', 'show');
                addnRemoveClassMenu2('active', 'show', 'not-active', 'hide');
            }
            else {
                return;
            }
        }
        else {
            return;
        }
    })

    ele[2].addEventListener('click', () => {
        if(!menuActive3.classList.contains('active') && !menuShow3.classList.contains('show')) {
            if(menuActive1.classList.contains('active') && menuShow1.classList.contains('show')) {
                addnRemoveClassMenu1('not-active', 'hide', 'active', 'show');
                addnRemoveClassMenu3('active', 'show', 'not-active', 'hide');
            }
            else if(menuActive2.classList.contains('active') && menuShow2.classList.contains('show')) {
                addnRemoveClassMenu2('not-active', 'hide', 'active', 'show');
                addnRemoveClassMenu3('active', 'show', 'not-active', 'hide');
            }
            else {
                return;
            }
        }
        else {
            return;
        }
    })
}

// setTimeout(focus, 100);

export {focus};