// animating curosr element

const clsOrId = [
    "rubber animate", "rubber", "letend",
    "Layer_1", "Layer_2", "btn-con magnet", 'skl-btn btn0',
    'skl-name', 'pro-link-01', 'pro-link-02', 'pro-link-03', 
    'pn01', 'pn02', 'pn03', 'contact-lnk', 'prl01', 'prl02', 
    'prl03',
]

let found = (key) => {
    let classFounOrNot = clsOrId.find((str) => {
        return str === key;
    });

    if(classFounOrNot === key) {
        return true;
    }

    return false;
}

let magnetEffect = () => {
    const btns = document.querySelectorAll(".magnet");
    
    btns.forEach((btn) => {
        btn.addEventListener("mousemove", (event) => {
            const position = btn.getBoundingClientRect();
            const x = event.pageX - position.left - position.width / 2;
            const y = event.pageY - position.top - position.height / 2;
            // console.log()
            btn.children[0].style.transform = "translate(" + x * 0.3 + "px, " + y * 0.5 + "px)";
        });
    });
    
    btns.forEach((btn) => {
        btn.addEventListener("mouseout", (event) => {
            btn.children[0].style.transform = "translate(0px, 0px)";
        });
    });
}


let dynamicMouse = () => {
    const ele = document.getElementsByClassName('cursor');
    document.addEventListener("mousemove", (event) => {
        let currOverEle = document.elementFromPoint(event.clientX, event.clientY);
        console.log(currOverEle.id);
        if(ele[0] === undefined) return;
        if(found(currOverEle.className) || found(currOverEle.id)) {
            ele[0].style.setProperty('opacity', 0.7);
            ele[0].style.setProperty('transform', "translateX(" + (event.clientX - 14) + "px)" + " " + "translateY(" + (event.clientY - 14) + "px)" + " " + "scale(" + 5 + ")");
        }
        else {
            ele[0].style.setProperty('opacity', 1);
            ele[0].style.setProperty('transform', "translateX(" + (event.clientX - 14) + "px)" + " " + "translateY(" + (event.clientY - 14) + "px)" + " " + "scale(" + 1 + ")");
        }
    });
}

export {dynamicMouse, magnetEffect};